import React from 'react';
import AbstractList from '../Core/AbstractList';


class Goods extends AbstractList {

	constructor(props) {
		super(props);
		this.getCreatePanel = this.getCreatePanel.bind(this);
	}

	getEntityName() {
		return "goods";
	}

	getSearchPanel() {
		return [
			{ id: "EQ:title", type: 'text', label: '' }
		]
	}

	getTableHead() {
		return ["", "", " ", "", ""];
	}


	getCreatePanel() {
		let value = (this.state.data && this.state.data.category ? this.state.data.category.title : '');
		let emptyArray = (value !== '' ? [this.state.data.category] : []);
		return [
			{ id: "title", type: 'text', label: '' },
			{
				id: "category", type: 'suggest', label: '', value: value,
				values: this.state.categories === undefined ? emptyArray : this.state.categories,
				fetch: this.fetchCategory.bind(this), clear: this.clearCategory.bind(this),
				convertVal: val => val.title, renderSuggestion: item => <span>{item.title}</span>,
				placeholder: ""
			},
		];
	}

	fetchCategory(value) {
		if (value.reason === 'input-changed') {
			this.rest.getBase("goodsCategory/search", { page: 0, pageSize: 20, 'LIKE:title': value.value })
				.then(resp => this.setState({ categories: resp.data.content }));
		}
	}

	clearCategory() {
		// do nothing
	}

	saveEntity(entity) {
		let categories = (this.state.data && this.state.data.category ? [this.state.data.category] : this.state.categories);
		let category = categories.filter(item => item.title === entity.category)[0];
		entity.category = category;
		super.saveEntity(entity);
	}



	convertToTableRow(data) {
		return (
			<tr key={"item_" + data.id}>
				<td>{data.id}</td>
				<td>{data.title}</td>
				<td>
					<i className="fa fa-times" aria-hidden="true" hidden={data.deleted}></i>
					<i className="fa fa-check" aria-hidden="true" hidden={!data.deleted}></i>
				</td>
				<td>{data.category ? data.category.title : ""}</td>
				<td>
					<i className="btn fa fa-pencil" aria-hidden="true" onClick={this.editEntity.bind(this, data.id)}></i>
				</td>
			</tr>
		);
	}

}


export default Goods;


